.search-container {
  text-align: center;
  margin-bottom: 20px;
}

.search-form {
  max-width: 500px;
  background-color: #fff;
  border-radius: 10px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
  box-shadow: 0 0 15px rgba(0, 0, 0, .2);
}

input[type="text"] {
  border: none;
  border-radius: 10px 0 0 10px;
  outline: none;
  flex-grow: 1;
  padding: 12px;
  font-size: 18px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 0 10px 10px 0;
  padding: 12px 24px;
  font-size: 18px;
}

button[type="submit"]:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, .3);
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  background-color: #f5f5f5;
  border-radius: 0 0 10px 10px;
  padding: 10px;
}

.info-item {
  margin: 0;
  padding: 5px 0;
}

#load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 10px;
  margin: 20px auto;
  padding: 12px 24px;
  font-size: 18px;
  display: none;
}

#load-more:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, .3);
}

.photo-card {
  background-color: #fff;
  border: 2px solid #3498db;
  border-radius: 8px;
  flex-direction: column;
  transition: transform .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.photo-card a {
  text-decoration: none;
}

.photo-card__image {
  max-width: 100%;
  height: auto;
  display: block;
}

.info {
  background-color: #f7f7f7;
